import React, { Component } from 'react'
import reactHtmlParser from "react-html-parser"
import {withTranslation} from 'react-i18next';

export class childrenSection extends Component {
    render() {
        const {t} = this.props;
        return (

            <div>
                <h2>{this.props.heading}</h2>
                    <p>{reactHtmlParser(this.props.description)}</p>
                    <ul>
                         {this.props.h2List && this.props.h2List.map(topP => <li>{reactHtmlParser(t(topP))}</li>)}
                     </ul>
            </div>
        )
    }
}

export default withTranslation() (childrenSection)
