import { externalUrls } from "./ExternalUrls";
import { withTranslation } from "react-i18next";
import ExternalRedirect from "./Component/ExternalRedirect/ExternalRedirect";

const supportedLanguages = ["en", "hn"];

class Routes extends React.Component {

  trackLanguage = () => {
    const pathnames = window.location.pathname.split("/");
    const lang = pathnames.length > 0 ? pathnames[1] : "";
    if (supportedLanguages.includes(lang))
      localStorage.setItem('niveshLanguage', lang);
    if ((supportedLanguages.includes(lang)) && (this.props.i18n.language !== lang)) {
      this.props.changeLanguage(lang);
    }
  }

  componentDidMount() {
    this.trackLanguage();
  }

  componentDidUpdate() {
    this.trackLanguage()
  }

  render() {

    return (
      <Switch>
        {externalUrls && externalUrls.map(externalUrl =><Route path={externalUrl.from}
          render={()=> <ExternalRedirect loc={externalUrl.to} />} /> )}
        {urls && urls.map((url)=> <Redirect from={url.from} to={{ pathname: url.to, state: { status: 301 }}}/>)}
        <Route exact path="/:lang/other" component={Home} />
        <Route exact path="/:lang/about-us" component={AboutUs} />
        <Route exact path="/:lang/contact-us" component={Contact} />
        <Route exact path="/:lang/partner/all-about-amfi-arn-code" component={ARN} />
        <Route exact path="/:lang/plan-for-retirement" component={Retirement} />
        <Route exact path="/:lang/build-long-term-wealth" component={BLT} />
        <Route exact path="/:lang/save-tax" component={SaveTax} />
        <Route exact path="/:lang/save-for-children" component={SFC} />
        <Route exact path="/:lang/partner/grow-your-mutual-fund" component={Grow} />
        <Route exact path="/:lang/partner/become-mutual-fund-distributors" component={BMF} />
        <Route exact path="/:lang/bond" component={Bond} />
        <Route exact path="/:lang/alternative-investment-fund" component={AIF} />
        <Route exact path="/:lang/national-pension-scheme" component={NPS} />
        <Route exact path="/:lang/fixed-deposit" component={FD} />
        <Route exact path="/:lang/peer-to-peer-lending" component={P2P} />
        <Route exact path="/:lang/peer-to-peer-lending/liquiloans" component={LiquiLoans} />
        <Route exact path="/:lang/peer-to-peer-lending/faircent" component={faircent} />
        <Route exact path="/:lang/capital-gain-bond" component={CapitalGainBond} />
        <Route exact path="/:lang/pms" component={PMS} />
        <Route exact path="/:lang/gold" component={Gold} />
        <Route exact path="/:lang/gold-mutual-fund" component={GoldFund} />
        <Route exact path="/:lang/digital-gold" component={DigitalGold} />
        <Route exact path="/:lang/p2p-fairassets" component={P2Pfair} />
        <Route exact path="/:lang/partner" component={Partner} />

        <Route exact path="/:lang/loan-against-securities" component={LoanAgainstSecurities} />
        <Route exact path="/:lang/loans/instant-home-loan" component={InstantHomeLoan} />
        <Route exact path="/:lang/fixed-deposit/bajaj-finance-ltd" component={BajajFinanceLtd} />
        <Route exact path="/:lang/fixed-deposit/hdfc-ltd" component={HDFC} />
        <Route exact path="/:lang/fixed-deposit/pnb-housing-finance-ltd/" component={PNB} />
        <Route exact path="/:lang/fixed-deposit/icici-home-finance-co-ltd" component={ICICI} />
        <Route exact path="/:lang/fixed-deposit/shriram-transport-finance-company-ltd" component={Shriram} />
        <Route exact path="/:lang/fixed-deposit/mahindra-finance-limited/" component={Mahindra} />
        <Route exact path="/:lang/mahindra" component={Mahindra} />
        <Route exact path="/:lang/pms/ask-group" component={AskGroup} />
        <Route exact path="/:lang/pms/tata-asset-management" component={TATA} />
        <Route exact path="/:lang/pms/invesco-asset-management" component={InvescoAsset} />
        <Route exact path="/:lang/pms/motilal-oswal-group" component={MotilalOswal} />
        <Route exact path="/:lang/pms/icici-prudential" component={ICICIPrudential} />

        <Route exact path="/:lang/nism-certification-exam" component={Certification} />
        <Route exact path="/:lang/terms-and-conditions" component={Terms} />
        <Route exact path="/:lang/privacy-and-policy" component={Policy} />
        <Route exact path="/:lang/bond/corporate-bond" component={CB} />
        <Route exact path="/:lang/bond/rbi-floating-rate-saving-bonds" component={RBI} />
        <Route exact path="/:lang/bond/non-convertible-debentures" component={NCD} />
        <Route exact path="/:lang/bond/sovereign-gold-bond" component={SGB} />


        <Route path={["/:lang/MutualFund", "/:lang/HybridMutualFund", "/:lang/DynamicAssetAllocationfunds", "/:lang/ArbitrageFund", "/:lang/AgressivehybridFunds", "/:lang/BalancedHybridFunds", "/:lang/MultiAssetAllocation", "/:lang/ConservativeHybridFunds", "/:lang/EquitySavingsMutualFund", "/:lang/DebtMutualFunds", "/:lang/MediumDurationFund", "/:lang/overnightmutualfunds", "/:lang/lowdurationfunds", "/:lang/dynamicbond", "/:lang/giltfund10yrs", "/:lang/LongDurationFund", "/:lang/ShortDurationFund", "/:lang/MoneyMarketFund", "/:lang/GiltFund", "/:lang/CorporateBondFund", "/:lang/FloaterMutualFund", "/:lang/LiquidMutualFund", "/:lang/MediumToLongDurationFund", "/:lang/UltraShortTermFund", "/:lang/BankandPSUDebtFund", "/:lang/CreditRiskFund", "/:lang/Equity", "/:lang/focusedfund", "/:lang/flexicapfunds", "/:lang/midcapfunds", "/:lang/multicapfunds", "/:lang/largecapfunds", "/:lang/smallcapfunds", "/:lang/largeandmidcapfunds", "/:lang/sectoralthermatic", "/:lang/valuefund", "/:lang/elssfunds", "/:lang/dividendyieldfunds", "/:lang/ContraFund", "/:lang/retirementfund", "/:lang/indexfund","/:lang/childrenFund","/:lang/FoFs"]} component={MutualFund} />

        <Route exact path="/:lang/calculators" component={calculator} />
        <Route
          exact
          path="/:lang/"
          render={redirection}
        />
        <Route exact path="/:lang/" component={Home} />
        <Route exact path="/" render={redirection} />
        <Route exact path="*" render={redirection} />
      </Switch>
    );
  }
}

 const isValidPath = (path) =>{
  if(path[path.length-1]==="/")
    path= path.slice(0, -1);

  return validPaths.includes(path);
}

const redirection = ((props) => {
  const isWithLanguage = supportedLanguages.includes(props.match.params.lang);
  const selectedLanguage = localStorage.getItem('niveshLanguage');
  const language = selectedLanguage ? selectedLanguage : 'en';
  let redirectUrl = language;


  if (props.match.params.lang && isValidPath(`/${props.match.params.lang}`)){
    redirectUrl = redirectUrl.concat(`/${props.match.params.lang}`);
  }
  if (!props.match.params.lang && props.match.params[0] && isValidPath(`${props.match.params[0]}`)) {
    redirectUrl = redirectUrl.concat(props.match.params[0]);
  }



  return (
    <>
      {isWithLanguage ?
        <Home {...props} />
        : <Redirect to={`/${redirectUrl}`} />
      }
    </>
  );
})

