  "/MutualFund",
  "/HybridMutualFund",
  "/DynamicAssetAllocationfunds",
  "/ArbitrageFund",
  "/AgressivehybridFunds",
  "/BalancedHybridFunds",
  "/MultiAssetAllocation",
  "/ConservativeHybridFunds",
  "/EquitySavingsMutualFund",
  "/DebtMutualFunds",
  "/MediumDurationFund",
  "/overnightmutualfunds",
  "/lowdurationfunds",
  "/dynamicbond",
  "/giltfund10yrs",
  "/LongDurationFund",
