  "/MoneyMarketFund",
  "/GiltFund",
  "/CorporateBondFund",
  "/FloaterMutualFund",
  "/LiquidMutualFund",
  "/MediumToLongDurationFund",
  "/UltraShortTermFund",
  "/BankandPSUDebtFund",
  "/CreditRiskFund",
  "/Equity",
  "/focusedfund",
  "/flexicapfunds",
