import React from 'react';
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import './Navbar.css';
import logo from '../../Layouts/Home/images/LOGO.png';
import {
  NavLink,
  Bars,
  NavMenu,
  NavBtn,
  NavBtnLink
} from './HeaderElements';


function Navmenu(props) {
  return (
    //<div className="container">
      //<div className="Navmenu">
      <Container>
        <Navbar variant="light" sticky="top" expand="md" collapseOnSelect>

          <Navbar.Brand>
            <a href="/">
              <img src={logo} width="240vw" height="auto" className='logo-img' /></a>{' '}
          </Navbar.Brand>
          <Navbar.Toggle className="coloring" />
          <Navbar.Collapse className="right-aligned">
            <Nav>
              <NavDropdown title="Products">
                <NavDropdown.Item href="/MutualFund">Mutual Fund</NavDropdown.Item>
                <NavDropdown.Item href="/fixed-deposit">Fixed Deposit</NavDropdown.Item>
                <NavDropdown.Item href="/pms">PMS</NavDropdown.Item>
                <NavDropdown.Item href="/peer-to-peer-lending">P2P Loan</NavDropdown.Item>
                <NavDropdown.Item href="/alternative-investment-fund">AIF</NavDropdown.Item>
                <NavDropdown.Item href="/national-pension-scheme">NPS</NavDropdown.Item>
                <NavDropdown.Item href="/bond">Bond</NavDropdown.Item>
                <NavDropdown.Item href="/gold">Gold</NavDropdown.Item>
              </NavDropdown>
              <NavDropdown title="Partner">
                <NavDropdown.Item href="/partner/become-mutual-fund-distributors">Become MF Distributor</NavDropdown.Item>
                <NavDropdown.Item href="/partner/grow-your-mutual-fund">Grow MF Business</NavDropdown.Item>
                <NavDropdown.Item href="/partner/all-about-amfi-arn-code">AMFI ARN Registration</NavDropdown.Item>
              </NavDropdown>
              <a href="https://app.nivesh.com/home/index" className="register-button">Login/Register</a>

              <NavDropdown title="Select Language" className='language-selector'>
                <NavDropdown.Item onClick={() => props.changeLanguage('en')} >English
                </NavDropdown.Item>
                <NavDropdown.Item onClick={() => props.changeLanguage('hn')} >Hindi</NavDropdown.Item>
              </NavDropdown>


            </Nav>
          </Navbar.Collapse>

        </Navbar>
        </Container>
     // </div>
   // </div>


  );
}

export default Navmenu;